import Avatar from "@mui/material/Avatar";
import "./TL.css";
import { Card } from "react-bootstrap";
// import { Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
const TL = ({ data, btnCl }) => {
  return (
    <>
      {data.map((i) => (
        <Card>
          <Avatar style={{ marginBottom: "2rem" }} className="" />
          <p className="tc">{i}</p>
          <div className="dt">
            <span className="tc">{i + "  Detail"}</span>
            {/* <button onClick={() => btnCl(i)}>Click</button> */}
            {/* <Link to={"/" + i} /> */}
          </div>
        </Card>
      ))}
    </>
  );
};
export default TL;
